	.buttonUp {
		display: none; }
	.NavbarMenu {
		ul {
			padding-left: 0;
			margin-top: 20px; } }
	.Footer {
		h2 {
			font-size: 24px; }
		.col-md-4:nth-child(1) {
			p {
